"use client";

import { observer } from "mobx-react-lite";
import { useStore } from "@/stores/StoreProvider";
import { useState } from "react";

import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogDescription,
} from "@/components/ui/dialog";
import { toast } from 'sonner'
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

function OtpModal() {
    const { userStore } = useStore();
    const [code, setCode] = useState("");

    const handleVerify = async () => {
        await userStore.verifyOtp(code);

        if(userStore.error){
            toast.error(userStore.error)
        }
    };

    return (
        <Dialog open={userStore.otpModalOpen}>
            <DialogContent className="max-w-sm">
                <DialogHeader>
                    <DialogTitle>Verify OTP</DialogTitle>
                    <DialogDescription>
                        We sent a 6-digit code to <b>{userStore.otpEmail}</b>.
                        Purpose: <b>{userStore.otpPurpose}</b>
                    </DialogDescription>
                </DialogHeader>

                <div className="space-y-3 mt-4">
                    <Input
                        placeholder="Enter OTP"
                        value={code}
                        onChange={(e) => setCode(e.target.value)}
                        maxLength={6}
                    />

                    {userStore.error && (
                        <p className="text-red-500 text-sm">{userStore.error}</p>
                    )}

                    <Button
                        className="w-full"
                        disabled={userStore.loading}
                        onClick={handleVerify}
                    >
                        {userStore.loading ? "Verifying..." : "Verify OTP"}
                    </Button>
                </div>
            </DialogContent>
        </Dialog>
    );
}

export default observer(OtpModal);
